<!--商品列表-->
<template>
  <article class="main-article">
    <!--面包屑-->
    <div class="navigation">
      <p v-if="!$route.query.keyword"><i  class="f12 lh300"  v-for=" (j,i) in crumbHover" > {{i>0?'>':''}} <a @click="i==1?goList():''" :class="{'hover':crumbHover.length>2}">{{j}}</a></i></p>
      <a class="f12 lh300" v-else>查询结果</a>
    </div>
    <div class="class-box pr" v-if="!$route.query.keyword">
      <ul :class="{'show':classShow}" >
        <li><span>品类</span></li>
        <li class="class-es" >
          <a v-for="(j,i) in thirdCategoryList" href="javascript:" @click="goList(j)" :class="{'c-green':j.categoryCode==$route.query.trdCategory}">{{j.categoryName}}</a>
        </li>
      </ul>
      <i class="show-icon pa f12 cursor " v-if="classHeight>47" @click="isShow(classShow);prvData=$route.query.prev">{{classShow?'收起':'展开'}}</i>
    </div>
    <div class="select-box mt20">
      <div class="select-icon" v-if="!$route.query.keyword">
        <span :class="{'hover':pagesCode.orderby=='C'}" @click="listSort(pagesCode.orderby='C')">综合</span>
        <span :class="{'hover':pagesCode.orderby=='a'||pagesCode.orderby=='A'}" @click="listSort(pagesCode.orderby=='a'?pagesCode.orderby='A':pagesCode.orderby='a')">销量{{pagesCode.orderby=='a'?'↑':'↓'}}</span>
        <span :class="{'hover':pagesCode.orderby=='b'||pagesCode.orderby=='B'}" @click="listSort(pagesCode.orderby=='b'?pagesCode.orderby='B':pagesCode.orderby='b')">价格{{pagesCode.orderby=='b'?'↑':'↓'}}</span>
      </div>
      <div class="select-icon" v-else>
        <span :class="{'hover':pagesCode.sort=='C'}" @click="listSort(pagesCode.sort='C')">综合</span>
        <span :class="{'hover':pagesCode.sort=='a'||pagesCode.sort=='A'}" @click="listSort(pagesCode.sort=='a'?pagesCode.sort='A':pagesCode.sort='a')">销量{{pagesCode.sort=='a'?'↑':'↓'}}</span>
        <span :class="{'hover':pagesCode.sort=='b'||pagesCode.sort=='B'}" @click="listSort(pagesCode.sort=='b'?pagesCode.sort='B':pagesCode.sort='b')">价格{{pagesCode.sort=='b'?'↑':'↓'}}</span>
      </div>

    </div>
    <div class="list-content-box">
      <ul v-if="commodityList">
        <li class="pr" v-for="(g,i) in commodityList.results" @click.stop="$router.push({name: 'Detail', params: {commodityCode: g.commodityCode}})" style="position: relative;">

          <template v-if="g.presell">
            <i class="iconfont icon-yushou"></i>
            <div class="trangle"></div>
          </template>
          <img :src="g.imageUrl" alt="">
          <span class="icon-no-stock" v-if="g.commodityStatus===3&&g.commodityStock===0">已售罄<br/>拼命补货中</span>
          <div class="list-content-text">
            <p>
              <span><i class="price b c-red f16">{{g.commoditySalesPrice|toF}}</i> <i class="del-tag price f12">{{g.commodityOriginalPrice|toF}}</i></span>
              <span> <i class="f999 f12 fr">{{g.praise}}%</i></span>
            </p>
            <span class="block f12 lh220 textcut" style="height: 60px; width: 100%;">
              {{g.commodityName}}
            </span>
          </div>
          <div class="btn-box">
            <i :class="{'hover':g.focus}" @click.stop="collect(g, g.commodityCode)"  ><em class=" iconfont  icon-guanzhu" style="vertical-align:text-top"></em> {{g.focus?'已关注':'关注'}}</i>

            <i  @click.stop="addCart($event, g)"><em class=" iconfont  icon-gouwuche" style="vertical-align:text-top"></em>  加入购物车</i>
          </div>
        </li>
        <li></li><li></li><li></li><li></li><li></li>
      </ul>
      <noFile v-if="commodityList===null"/>
    </div>
    <div class="fr" >
      <Pages  v-if="commodityList" :pageNumC="pagesCode.pageNum" :pageSizeC="pagesCode.pageSize" :totalC="pagesCode.total" @pageDone="getPageDone" />
    </div>
    <Flyball ref="flyball" />
    <Bar :type="1"/>
  </article>
</template>

<script>
  import Bar from '@/components/Bar.vue'
  import noFile from '@/components/noFile'
  import Pages from '@/components/Pages'
  import Flyball from '@/components/Flyball.vue'



export default {
  name: 'List',
  components:{noFile,Pages,Flyball,Bar},
  data () {
    return {
      classShow:false,
      category:"",
      commodityList:undefined,
      thirdCategoryList:null,
      pagesCode:{},
      classHeight:0,
      prvData:"",
      sessionCache:"",
      uriParams:null
    }
  },
  computed: {
    crumb(){
      return decodeURIComponent(decodeURIComponent(this.$route.query.crumb))
    },

    crumbHover(){
      return this.crumb.split(">")
    },
    loginStatus () {
      return this.$store.state.loginStatus
    }

  },
  created () {

  },
  mounted(){
    !sessionStorage.getItem("sessionCache")?sessionStorage.setItem('sessionCache',this.$route.fullPath):'';// 记录首次大类
    this.getCommodityList();
    this.getThirdCategoryList();
    this.setShowClass();
  },
  watch:{
    $route(res){
      this.getCommodityList();
      this.getThirdCategoryList();
      this.setShowClass();

      let prv=res.query.prev;
      if(this.prvData!=prv){
        this.classShow=false;
        this.prvData=prv
      }
    }
  },

  methods: {

    // 关注商品
    collect (g,id) {
      // 如果已经关注了，则取消
      if (g.focus===true) {
        this.axios.get(this.api.removeCollection + '?commodityCode=' + id).then(res => {
          if (res.status === 200 && res.data.code < 300) {
          g.focus = false
        }
      })
      } else {
        this.axios.get(this.api.collect + '?commodityCode=' + id).then(res => {
          if (res.status === 200 && res.data.code < 300) {
          g.focus = true
        }
      })
      }
    },


    //排序
    listSort(){
      this.getCommodityList()
    },

    // 加入购物车
    addCart (event, item) {
      if (this.loginStatus) {
        let param = {
          commodityCode: item.commodityCode,
          num: 1,
          secKill: false
        }
        this.axios.post(this.api.save, param).then(res => {
          if (res.status === 200 && res.data.code === 200) {
            // 加入购物车动画
            this.$refs.flyball.drop(event)
            setTimeout(() => {
              this.$store.dispatch('getCartNum')
            }, 600)
          } else {
            this.$message({
              message: res.data.remark,
              type: 'warning'
            })
          }
        })
      } else {
        // 如果未登录，则跳转到登录页面
        this.$router.push({ name: 'Login' })
      }
    },

    /*展开收起超出等状态判断*/
    setShowClass(){
      this.$nextTick(()=>{
        let d=document.querySelector(".class-es");
        if(!d){return false}
        this.classHeight= d.scrollHeight;
      })
    },

    /*连接跳转*/
    goList(d){
      let params=this.$pub.toParams(this.$route.fullPath)||{};
      let u=this.crumb.split(">");
      if(d){
        params.trdCategory=d.categoryCode;//新增子类
        u.splice(2,1, d.categoryName);
      }else{
        u.splice(2,1);
       delete  params.trdCategory;
      }
      let crumb=encodeURIComponent(encodeURIComponent(u.join(">")));
      params.crumb=crumb;


      let str=this.$pub.toQuery(params)
      this.$router.push(`/list?${str}`)

    },
    /*获取第三类*/
    getThirdCategoryList(){
      let parentId=this.$route.query.parent;
      let prevId=this.$route.query.prev;
      let that=this;
      let mainCategory=JSON.parse(localStorage.localStorageMainCategory||'[]');

      mainCategory.forEach(function(x){
        if(x['categoryCode']==parentId){
          x['secondCategoryList'].forEach(function(y){
            if(y['categoryCode']==prevId){
              that.thirdCategoryList=y['thirdCategoryList']||[]
            }
          })
        }
      })
    },

    isShow(dt){
     this.classShow=!dt;
    },
    /*商品列表*/
    getCommodityList(res){
      this.$route.query.keyword?this.pagesCode.keyword=(decodeURIComponent(this.$route.query.keyword)):this.pagesCode.keyword='';
      this.pagesCode.category=this.$route.query.trdCategory||this.$route.query.category;
      this.$route.query.keyword?this.pagesCode.sort=this.pagesCode.sort||"C":this.pagesCode.orderby=this.pagesCode.orderby||"C";
      this.pagesCode.pageNum=res&&res.pageNum||1;
      this.pagesCode.pageSize=this.pagesCode.pageSize||20;
      this.pagesCode.width=200;
      this.pagesCode.height=200;

      let httpUrl=this.$route.query.keyword?(this.axios.get(`${this.api.searchlist}?${this.$pub.toQuery(this.pagesCode)}`)):(this.axios.post(this.api.getCommodityList,this.pagesCode))

      httpUrl.then(res => {
        if (res.status === 200 && res.data.code === 200) {
          window.scrollTo(0,0)
          this.commodityList = (res.data.result&&res.data.result.pageeCommodity)||null;
          this.pagesCode.total=this.commodityList&&this.commodityList.total;
        }
      })
    },
    getPageDone(res){
      this.getCommodityList(res)
    }


  }
}
</script>

<style scoped>
  .class-box{border:1px solid #eee;  }
  .class-box ul{display: flex; align-items:stretch; overflow: hidden; height: 47px ;transition: all 0.5s ;   }
  .class-box ul.show{height: auto}
  .class-box  li{  padding: 12px}
  .class-box  li:nth-child(1){width: 140px; background: #efffdb; display: flex;flex-direction: column; justify-content: center  }
  .class-box  li:nth-child(2){width: 900px; height: auto }
  .class-box  li a{padding:6px 20px; display: inline-block}
  .show-icon{ top: 10px; right: 10px}

  .select-box{ display: flex; justify-content: space-between; background: #eee;padding: 6px}
  .select-icon span{ width: 66px; height: 30px; background: #fff; text-align: center; line-height: 30px; display: inline-block; cursor: pointer}
  .select-icon span.hover{background:#ffc000;  color: #fff }

  .list-content-box ul { display: flex;  flex-wrap: wrap;  padding: 10px 0 20px 0; justify-content: space-between;align-items: flex-start;   }
  /*.list-content-box ul:after{content: "";  flex: auto;}*/
  .list-content-box li{ width: 220px; padding: 10px; box-sizing: border-box; cursor: pointer; transition: all 0.5s;    }
  .list-content-box li:hover{ box-shadow: 0px 0px 15px 0px #ccc;}
  .del-tag{text-decoration: line-through}
    i.price:before{content: "¥"}
  .list-content-text{ padding: 4px 0}
  .list-content-text p{ display: flex; justify-content: space-between; padding: 4px 0}
  .btn-box{ display: flex; max-width: 100%}
  .btn-box i{ display: inline-block; width: 40%; cursor: pointer; font-size: 12px; color: #999;  padding: 6px 10px;text-align: center; border:1px solid #eee; box-sizing: border-box}
  .btn-box i:nth-child(2){ margin-left: -1px; width: 60%}
  .btn-box i:hover,.btn-box i.hover{ color:#ffc000 }


  .navigation i:nth-child(2) a.hover{cursor: pointer}
  .navigation i:nth-child(2) a.hover:hover{ color: #7bbe2b}






</style>





