<template>

  <div class="pages">
    <i class="prev" @click="pageGo(1)" :class="{'disabled':pageNum==1}">首页</i>
    <i class="prev" @click="prev()" :class="{'disabled':pageNum==1}">上一页</i>
    <i @click="pageGo(k)" v-for="(k,i) in pageNums" :class="{'hover':pageNum==k}">{{k}}</i>
    <i class="next" @click="next()" :class="{'disabled':pageNum==maxPage}" >下一页</i>
    <i class="prev" @click="pageGo(maxPage)" :class="{'disabled':pageNum==maxPage}">末页</i>
    <em>共{{total}}条</em>
  </div>
</template>

<script>
  export default{
    name:"Pages",
    props:["pageNumC","pageSizeC","totalC"],
    data () {
      return {
        pageNum:this.pageNumC,
        pageSize:this.pageSizeC,
        total:this.totalC,
        pageNums:[],
        pageSteps:5
      }
    },
    computed:{
      maxPage(){
        return Math.ceil(this.total/this.pageSize)
      }
    },
    watch:{
      totalC(res){
        this.total=res;
        this.setNum(this.pageNum)
      },
      pageNumC(res){
        //一个页面同时2个相同的分页请开启
        this.pageNum=res;
        //this.setNum(this.pageNum)
      }
    },
    mounted(){
      this.setNum(this.pageNum)
    },
    methods:{
      prev(){
        if(this.pageNum==1){
          return false
        }
        this.pageNum>1?this.pageNum--:"";
        this.setNum( this.pageNum)
        this.pageDone();
      },
      next(){

        if(this.pageNum==this.maxPage){
          return false
        }
        this.pageNum<this.maxPage?this.pageNum++:"";
        this.setNum(this.pageNum)
        this.pageDone();
      },
      pageGo(p){
        this.pageNum=p;
        this.setNum(this.pageNum)
        this.pageDone();
      },

      setNum(cur){
          let arr = [],
            cut = Math.floor(this.pageSteps/2),
            pages= this.maxPage,
            left = cur-cut,
            right = cur+cut;

          if (left < 1) {
            right = right + (1 - left);
            left = 1
          }
          if (right > pages) {
            left = left - (right - pages);
            right = pages
          }
          if (left < 1){
            left = 1
          }

          for (let i = left; i <= right; i++) {
            arr.push(i)
          }
          this.pageNums= arr
      },
      pageDone(){
        this.$emit("pageDone",{pageNum:this.pageNum})
      }


    }


  }
</script>

<style scoped>

  .pages{width: 700px; display: flex; justify-content: flex-end; flex-wrap: nowrap;   }
  .pages i,.pages em{ display: inline-block; padding: 4px 10px; text-align: center; border: 1px solid #ddd;background: #fff; margin: 0 6px; cursor: pointer}
  .pages em{ border: none; padding-left: 0px}
  .pages i.hover,.pages i:hover{ background:#FFc000; }
  .pages i.disabled{ background: #eee;cursor: default}

</style>



